import React from 'react';
import { Typography, Stack, Button } from '@mui/material';
import { v4 as uuidv4 } from 'uuid';
import BodyPartImage from '../assets/icon/body-part.png';
import TargetImage from '../assets/icon/target.png';
import EquipmentImage from '../assets/icon/equipment.png';

const Detail = ({item}) => {
  const { bodyPart, gifUrl, name, target, equipment } = item;
  const extraDetail = [
    {
      icon: BodyPartImage,
      name: bodyPart,
    },
    {
      icon: TargetImage,
      name: target,
    },
    {
      icon: EquipmentImage,
      name: equipment,
    },
  ];


  return (
    <Stack 
    key={uuidv4()} gap="60px" 
    sx={{ flexDirection: { lg: 'row' }, p: '20px', alignItems: 'center' }}
    >
      <img 
        src={gifUrl} alt={name} 
        loading="lazy" 
        className="detail-image" 
      />
      <Stack 
        sx={{ gap: { lg: '35px', xs: '20px' } }}>
        <Typography 
          sx={{ fontSize: { lg: '64px', xs: '30px' } }} 
          fontWeight={700} 
          color='var(--secondary-color)'
          textTransform="capitalize">
          {name}
        </Typography>
        <Typography 
          sx={{ fontSize: { lg: '24px', xs: '18px' } }} 
          color='var(--secondary-color)'
          >
          Exercises keep you strong.{' '}
          <span 
          style={{ textTransform: 'capitalize' }}
          >{name}</span> bup is one
          of the best <br /> exercises to target your {target}. It will help you improve your{' '}
          <br /> mood and gain energy.
        </Typography>
        {extraDetail?.map((item) => (
          <Stack 
          key={item.name} 
          direction="row" 
          gap="24px" 
          alignItems="center">
            <Button 
            sx={{ background: '#FFF2DB', borderRadius: '50%', width: '100px', height: '100px' }
          }>
              <img 
              src={item.icon} 
              alt={bodyPart} 
              style={{ width: '50px', height: '50px' }} />
            </Button>
            <Typography 
              color='var(--secondary-color)'
              textTransform="capitalize" 
              sx={{ fontSize: { lg: '30px', xs: '20px' } }}>
              {item.name}
            </Typography>
          </Stack>
        ))}
      </Stack>
    </Stack>
  );
};

export default Detail;